import React, { useEffect, useState, useRef } from 'react';

import PostTableOfContents from '~components/PostTableOfContents';
import { ArticleSection, KeyVisualSection } from '~components/Section';
import { Post } from '~types/global';

interface PostContainerProps {
  post: Post;
}

const PostContainer = ({
  post,
}: PostContainerProps) => {
  const [tableOfContentsOffset, setTableOfContentsOffset] = useState(0);
  const keyVisualRef = useRef<HTMLElement>(null);

  useEffect(() => {
    const listener = () => {
      const { current: keyVisual } = keyVisualRef;

      if (keyVisual) {
        const keyVisualRect = keyVisual.getBoundingClientRect();
        const { pageYOffset } = window;
        const yOffset = keyVisualRect.height > pageYOffset
          ? keyVisualRect.height
          : pageYOffset;

        setTableOfContentsOffset(yOffset);
      }
    };

    listener();
    window.addEventListener('scroll', listener);

    return () => {
      window.removeEventListener('scroll', listener);
    };
  }, []);

  return (
    <>
      <KeyVisualSection ref={keyVisualRef} post={post} />
      {(post.html && post.tableOfContents) && (
        <>
          <ArticleSection html={post.html} />
          <PostTableOfContents
            html={post.tableOfContents}
            offset={tableOfContentsOffset}
          />
        </>
      )}
    </>
  );
};

export default PostContainer;
